<template>
    <div class="car">
        <div class="common-layout">
            <el-container>
                <!--  -->
                <el-header>
                    <div class="head">
                        <!--  -->
                        <el-button type="primary" plain @click="handleDel" :icon="Delete"></el-button>
                        <!--  -->
                        <el-button type="primary" plain @click="handleDialog(1)" :icon="CirclePlus"></el-button>
                        <!--  -->
                        <el-dialog v-model="dialogFormVisible" :title="diaTitle" width="60%" @close="formClose">
                            <el-form :model="form" :rules="rules" label-width="30%" ref="form">
                                <el-form-item label="" prop="ownerName">
                                    <el-select v-model="form.ownerId" clearable filterable placeholder=" "
                                        @change="selected()">
                                        <el-option v-for="item in options" :key="item.id" :label="item.name"
                                            :value="item.id" />
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="" prop="carNum">
                                    <el-input v-model="form.carNum" clearable />
                                </el-form-item>
                                <el-form-item label="" prop="color">
                                    <el-input v-model="form.color" clearable />
                                </el-form-item>
                                <el-form-item label="" prop="picture">
                                    <!--  -->
                                    <el-upload :drag="true" class="avatar-uploader" :action="url+actionImg"
                                        :show-file-list="false" :on-success="handleAvatarSuccess"
                                        :before-upload="beforeAvatarUpload">
                                        <el-icon class="avatar-uploader-icon">
                                            <img v-if="imageUrlF" :src="imageUrlF" class="avatar" />
                                            <el-icon class="avatar-uploader-plus">
                                                <component :is="Plus"></component>
                                            </el-icon>
                                        </el-icon>
                                    </el-upload>
                                </el-form-item>
                                <el-form-item label="" prop="remark">
                                    <el-input v-model="form.remark" type="textarea" :rows="5" />
                                </el-form-item>
                            </el-form>
                            <template #footer>
                                <span class="dialog-footer">
                                    <el-button @click="dialogFormVisible = false"></el-button>
                                    <el-button type="primary" @click="handleSave()">
                                        
                                    </el-button>
                                </span>
                            </template>
                        </el-dialog>

                        <!--  -->
                        <el-input class="inpSearch" v-model="searchMap.selcontent" placeholder=""
                            :prefix-icon="Search" clearable @keyup.enter.native="searchMap.pageNum=1;search()">
                            <template #append>
                                <el-select v-model="searchMap.sel" style="width: 150px;background-color: #f3efff;">
                                    <el-option label="" value="ownerName" />
                                    <el-option label="" value="carNum" />
                                    <el-option label="" value="color" />
                                </el-select>
                            </template>
                        </el-input>
                        <!--  -->
                        <div class="date">
                            <el-date-picker v-model="selectDate" type="daterange" range-separator=""
                                start-placeholder="" end-placeholder="" size="large" @change="dateChange" />
                        </div>
                        <!--  -->
                        <el-button type="primary" plain @click="searchMap.pageNum=1;search()" :icon="Search">
                        </el-button>
                    </div>
                </el-header>
                <!--  -->
                <el-main>
                    <!--  -->
                    <el-table v-loading="loading" element-loading-text="..." :data="List" border size="large"
                        @selection-change="handleSelectionChange" :header-cell-style="{
                            'background-color': '#d4cdf0',
                            'color': '#877fa6',
                            'border-bottom': '1px #877fa6 solid'
                        }" :default-sort="{prop: 'id', order: 'ascending'}">
                        <el-table-column width="50" type="selection" />
                        <el-table-column fixed prop="id" label="ID" width="80" sortable />
                        <el-table-column prop="picture" label="" width="150">
                            <template v-slot="scope">
                                <el-image :src="url+scope.row.picture" style="width: 90px;cursor: pointer;" fit="cover"
                                    :preview-src-list="srcList" @click="imagePreview(scope.row)"
                                    :preview-teleported="true" loading="lazy" @close="imageClose">

                                </el-image>
                            </template>
                        </el-table-column>
                        <el-table-column prop="ownerName" label="" width="130" />
                        <el-table-column prop="carNum" label="" width="300" />
                        <el-table-column prop="color" label="" width="300" />
                        <el-table-column prop="remark" label="" width="526" />
                        <el-table-column prop="createTime" label="" width="180" sortable />
                        <el-table-column fixed="right" label="" width="120">
                            <template #default="scope">
                                <!--  -->
                                <el-button class="btnEsit" link @click="handleDialog(2,scope.row)" :icon="Edit">
                                </el-button>
                                <!--  -->
                                <el-button class="btnDel" link @click="delId=scope.row;dialogVisible = true;"
                                    :icon="Delete">
                                </el-button>

                                <!---->
                                <el-dialog v-model="dialogVisible" title="" width="30%" :modal="false"
                                    :show-close="false">
                                    <span></span>
                                    <template #footer>
                                        <span class="dialog-footer">
                                            <el-button @click="dialogVisible = false"></el-button>
                                            <el-button type="primary" @click="handleDelete()">
                                                
                                            </el-button>
                                        </span>
                                    </template>
                                </el-dialog>

                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
                <!--  -->
                <el-footer>

                    <!--  -->
                    <el-pagination v-model:currentPage="currentPage" v-model:page-size="searchMap.pageSize"
                        :page-sizes="[5,10,20,50]" layout="total, sizes, prev, pager, next, jumper" :total="total"
                        @size-change="handleSizeChange" @current-change="handleCurrentChange" />
                </el-footer>
            </el-container>
        </div>
    </div>
</template>


<script>
    import axios from '@/utils/request'
    import {
        Search,
        Delete,
        Edit,
        CirclePlus,
        Plus,
    } from '@element-plus/icons-vue'

    export default {
        name: 'CarView',
        components: {},
        inject: ['reload'],
        setup() {
            return {
                // 
                Search,
                Delete,
                Edit,
                CirclePlus,
                Plus,
            }
        },
        data() {

            return {
                url: 'http://localhost:8088',
                loading: true,
                //
                List: [],
                //
                multipleSelection: [],
                //
                selectDate: ['', ''],
                //
                searchMap: {
                    "startTime": "",
                    "endTime": "",
                    "sel": "",
                    "selcontent": "",
                    "pageNum": 1,
                    "pageSize": 10
                },
                //
                total: 10,
                //
                currentPage: 1,
                // 
                dialogFormVisible: false,
                //
                form: {},
                //
                diaTitle: '',
                //
                dialogVisible: false,
                //
                rules: {
                    ownerName: [{
                        required: true,
                        max: 255,
                        message: '',
                        trigger: 'blur'
                    }],
                    remark: [{
                        required: false,
                        max: 500,
                        message: '500',
                        trigger: 'blur'
                    }],
                    carNum: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    color: [{
                        required: true,
                        max: 255,
                        message: '',
                        trigger: 'blur'
                    }],
                    picture: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                },
                // id
                delId: '',
                //id
                delIds: [],
                //select
                options: [],
                //
                imageUrlF: '',
                //
                // imageUrlB: '',
                //
                imagesUrl: [],
                //
                srcList: [],
                //
                actionImg: '/file/car',
            }
        },
        created() {
            this.search()
        },
        mounted() {
            //sel
            this.searchOptions()
        },

        methods: {

            //+
            search() {
                axios.post("/car/selpage", this.searchMap).then((res) => {
                    // console.log(this.searchMap)
                    if (res == null || res.length == 0) this.loading = true
                    else {
                        this.loading = false
                        this.List = res.data
                        this.total = res.total
                        console.log(res)
                    }
                })
            },
            //
            handleSizeChange(pageSize) {
                this.searchMap.pageSize = pageSize;
                this.search();
                console.log('pageSize' + this.searchMap.pageSize)
            },
            //
            handleCurrentChange(pageNum) {
                this.searchMap.pageNum = pageNum;
                this.search();
                console.log('pageNum' + this.searchMap.pageNum)
            },

            //table 
            handleSelectionChange(val) {
                //
                this.multipleSelection = val;
            },
            //
            handleDialog(val, row) {
                if (val == 1) {
                    this.form = []
                    this.diaTitle = ''
                } else {
                    this.diaTitle = ''
                    //id
                    const id = JSON.parse(JSON.stringify(row)).id
                    axios.get("/car/selbyid?id=" + id).then((res) => {
                        this.form = res.data;
                        this.imageUrl = this.url + this.form.picture
                    });


                }
                this.dialogFormVisible = true
            },
            //
            handleAvatarSuccess(res, file) {
                if (res.flag) {
                    this.$message({
                        message: res.message,
                        type: 'success'
                    });
                    this.imageUrlF = URL.createObjectURL(file.raw)
                    this.form.picture = res.data
                    //
                    this.imagesUrl.push(res.data)
                } else {
                    this.$message({
                        message: res.message,
                        type: 'warning'
                    });
                }
            },
            //
            beforeAvatarUpload(file) {
                if (file.size > 10000000) {
                    this.$message({
                        message: '10M',
                        type: 'warning'
                    });
                }
            },
            //
            formClose() {
                this.imageUrl = ''
                this.imageUrlF = ''
                this.imagesUrl.forEach(item => {
                    axios.get("/file/delcar?fileName=" + item).then((res) => {
                        if (res.flag) {
                            console.log("" + item + "")
                        }
                    });
                })
                this.imagesUrl = []
            },
            //
            handleSave() {
                var method = ''
                var obj = {}
                //
                this.imagesUrl.pop()
                this.imagesUrl.forEach(item => {
                    axios.get("/file/delcar?fileName=" + item).then((res) => {
                        if (res.flag) {
                            console.log("" + item + "")
                        }
                    });
                })
                this.imageUrlF = ''
                this.imageUrl = ''
                this.imagesUrl = []
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.diaTitle == '') {
                            method = 'add'
                        } else {
                            method = 'update'
                        }
                        obj = {
                            ...this.form
                        }
                        axios.post("/car/" + method, obj).then((res) => {
                            if (res.flag) {
                                this.$message({
                                    message: res.message,
                                    type: 'success'
                                });
                            } else {
                                this.$message({
                                    message: '',
                                    type: 'error'
                                });
                            }
                            this.reload()
                        });

                        this.reload() // 
                    } else {
                        return false;
                    }
                })
            },
            //
            handleDel() {
                const dataDel = this.multipleSelection
                var id
                //
                if (dataDel.length != 0) {
                    dataDel.forEach(item => {
                        // 
                        id = JSON.parse(JSON.stringify(item)).id
                        this.delIds.push(id)
                        //
                        var pic
                        axios.get("/car/selbyid?id=" + id).then((res) => {
                            pic=this.url+ res.data.picture
                        })
                        axios.get("/file/delcar?fileName=" + pic).then((res) => {
                            if (res.flag) {
                                console.log("" + pic + "")
                            }
                        });
                    })
                    axios.post("/car/del", this.delIds).then((res) => {
                        if (res.flag) {
                            this.$message({
                                message: res.message,
                                type: 'success'
                            });
                        } else {
                            this.$message({
                                message: '',
                                type: 'error'
                            });
                        }
                        this.reload()
                    });
                    this.reload()
                    this.dialogVisible = false;
                    this.delIds = []
                    this.delId = ''
                }
                //
                else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                }
            },
            //
            handleDelete() {
                const id = JSON.parse(JSON.stringify(this.delId)).id
                this.delIds.push(id)
                axios.post("/car/del", this.delIds).then((res) => {
                    if (res.flag) {
                        this.$message({
                            message: res.message,
                            type: 'success'
                        });
                    } else {
                        this.$message({
                            message: '',
                            type: 'error'
                        });
                    }
                    this.reload()
                });
                //
                var pic
                axios.get("/car/selbyid?id=" + id).then((res) => {
                    pic=this.url+ res.data.picture
                })
                axios.get("/file/delcar?fileName=" + pic).then((res) => {
                    if (res.flag) {
                        console.log("" + pic + "")
                    }
                });
                this.reload()
                this.dialogVisible = false;
                this.delIds = []
                this.delId = ''
            },
            //
            searchOptions() {
                axios.get("/owner/selall").then((res) => {
                    if (res == null || res.length == 0) this.loading = true
                    else {
                        this.loading = false
                        this.options = res.data
                    }
                })
            },
            //idform
            selected() {
                axios.get("/owner/selbyid?id=" + this.form.ownerId).then((res) => {
                    this.form.ownerName = res.data.name
                });
            },
            
            //searcHMap
            dateChange() {
                //
                const daterange = JSON.parse(JSON.stringify(this.selectDate))
                if (daterange == null || daterange.length == 0) {
                    this.searchMap.startTime = '';
                    this.searchMap.endTime = '';
                } else {
                    //searchMap
                    if (daterange[0] != null && daterange[0] != '') {
                        this.searchMap.startTime = daterange[0];
                    }
                    if (daterange[1] != null && daterange[1] != '') {
                        this.searchMap.endTime = daterange[1];
                    }
                }

            },
            //
            imagePreview(e) {
                this.srcList.push(this.url + e.picture)
            },
            //
            imageClose() {
                this.srcList = []
            },
        }
    }
</script>

<style scoped src="@/assets/css/content.css">


</style>
