<template>
    <div class="personal">
        <el-container>
            <el-aside>
                <el-image style="width: 250px; height: 300px;bottom: 300px;position:absolute;" :src="url+User.picture"
                    fit="contain"></el-image>
            </el-aside>
            <el-main>
                <el-descriptions title="" :column="2" direction="vertical" border>
                    <el-descriptions-item label="ID">{{User.id}}</el-descriptions-item>
                    <el-descriptions-item label="" :span="2">{{User.type==0?'':User.type==1?'':''}}
                    </el-descriptions-item>
                    <el-descriptions-item label="" :span="2">{{User.loginName}}</el-descriptions-item>
                    <el-descriptions-item label="">{{User.name}}</el-descriptions-item>
                    <el-descriptions-item label="">{{User.sex}}</el-descriptions-item>
                    <el-descriptions-item label="" :span="2">{{User.telephone}}</el-descriptions-item>
                    <el-descriptions-item label="">{{User.email}}</el-descriptions-item>
                </el-descriptions>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import axios from '@/utils/request'
    export default {
        name: "Personal",
        setup() {},

        data() {
            return {
                url: 'http://localhost:8088',
                user: sessionStorage.getItem('user'),
                User: {
                    "username": sessionStorage.getItem('user')
                },
            }

        },
        created() {

            if (this.user == null) {
                this.$message({
                    message: '',
                    type: 'error'
                })
            } else {
                //
                axios.post("/manager/selpic", this.User).then((res) => {
                    if (res.flag) {
                        this.User = res.data
                        console.log(this.User)
                    } else {
                        this.$message({
                            message: '',
                            type: 'error'
                        })
                    }
                })
            }
        },
        methods: {

        },
    }
</script>

<style>
    .el-descriptions__label.el-descriptions__cell.is-bordered-label {
        background: #f3efff;
    }
</style>
