<template>
    <div class="complaint">
        <div class="common-layout">
            <el-container>
                <!--  -->
                <el-header>
                    <div class="head">
                        <!--  -->
                        <el-button type="primary" plain @click="handleDel" :icon="Delete"></el-button>
                        <!--  -->
                        <el-button type="primary" plain @click="handleDialog(1)" :icon="CirclePlus"></el-button>
                        <!--  -->
                        <el-dialog v-model="dialogFormVisible" :title="diaTitle" width="60%">
                            <el-form :model="form" :rules="rules" label-width="30%" ref="form">
                                <el-form-item label="" prop="buildingName">
                                    <el-select v-model="form.buildingId" clearable filterable placeholder=" "
                                        @change="selected()" @clear="clear()">
                                        <el-option v-for="item in options" :key="item.id" :label="item.name"
                                            :value="item.id" />
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="" prop="ownerName">
                                    <el-select v-model="form.ownerId" clearable filterable placeholder=" "
                                        @change="selected2()" :disabled="form.buildingId==null?true:false">
                                        <el-option v-for="item in options2" :key="item.id" :label="item.name"
                                            :value="item.id" />
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="" prop="status">
                                    <el-radio-group v-model="form.status">
                                        <el-radio label="0"></el-radio>
                                        <el-radio label="1"></el-radio>
                                    </el-radio-group>
                                </el-form-item>
                                <el-form-item label="" prop="description">
                                    <el-input v-model="form.description" type="textarea" :rows="5" />

                                </el-form-item>
                                <el-form-item label="" prop="remark">
                                    <el-input v-model="form.remark" type="textarea" :rows="5" />
                                </el-form-item>
                            </el-form>
                            <template #footer>
                                <span class="dialog-footer">
                                    <el-button @click="dialogFormVisible = false"></el-button>
                                    <el-button type="primary" @click="handleSave()">
                                        
                                    </el-button>
                                </span>
                            </template>
                        </el-dialog>

                        <!--  -->
                        <!--                        <el-input class="inpSearch" v-model="searchMap.name" placeholder=""
                            :prefix-icon="Search" clearable @keyup.enter.native="searchMap.pageNum=1;search()" /> -->
                        <el-input class="inpSearch" v-model="searchMap.selcontent" placeholder=""
                            :prefix-icon="Search" clearable @keyup.enter.native="searchMap.pageNum=1;search()">
                            <template #append>
                                <el-select v-model="searchMap.sel" style="width: 150px;background-color: #f3efff;">
                                    <el-option label="" value="buildingName" />
                                    <el-option label="" value="ownerName" />
                                </el-select>
                            </template>
                        </el-input>
                        <!--  -->
                        <div class="date">
                            <el-date-picker v-model="selectDate" type="daterange" range-separator=""
                                start-placeholder="" end-placeholder="" size="large" @change="dateChange"
                                value-format="YYYY-MM-DD hh:mm:ss" />
                        </div>
                        <!--  -->
                        <el-button type="primary" plain @click="searchMap.pageNum=1;search()" :icon="Search">
                        </el-button>
                    </div>
                </el-header>
                <!--  -->
                <el-main>
                    <!--  -->
                    <el-table v-loading="loading" element-loading-text="..." :data="List" border size="large"
                        @selection-change="handleSelectionChange" :header-cell-style="{
                            'background-color': '#d4cdf0',
                            'color': '#877fa6',
                            'border-bottom': '1px #877fa6 solid'
                        }" :default-sort="{prop: 'id', order: 'ascending'}">
                        <el-table-column width="50" type="selection" />
                        <el-table-column fixed prop="id" label="ID" width="80" sortable />
                        <el-table-column prop="buildingName" label="" width="300" />
                        <el-table-column prop="ownerName" label="" width="130" />
                        <el-table-column prop="description" label="" width="526" />
                        <el-table-column prop="remark" label="" width="526" />
                        <el-table-column prop="createTime" label="" width="180" sortable />
                        <el-table-column fixed="right" prop="status" label="" width="130" sortable><template
                                #default="scope">
                                <el-tag :type="scope.row.status== '0' ? '':'success'">
                                    {{scope.row.status== '0' ? '' : ''}}
                                </el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column fixed="right" label="" width="120">
                            <template #default="scope">
                                <!--  -->
                                <el-button class="btnEsit" link @click="handleDialog(2,scope.row)" :icon="Edit">
                                </el-button>
                                <!--  -->
                                <el-button class="btnDel" link @click="delId=scope.row;dialogVisible = true;"
                                    :icon="Delete">
                                </el-button>

                                <!---->
                                <el-dialog v-model="dialogVisible" title="" width="30%" :modal="false"
                                    :show-close="false">
                                    <span></span>
                                    <template #footer>
                                        <span class="dialog-footer">
                                            <el-button @click="dialogVisible = false"></el-button>
                                            <el-button type="primary" @click="handleDelete()">
                                                
                                            </el-button>
                                        </span>
                                    </template>
                                </el-dialog>

                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
                <!--  -->
                <el-footer>
                    <!--  -->
                    <el-pagination v-model:currentPage="currentPage" v-model:page-size="searchMap.pageSize"
                        :page-sizes="[5,10,20,50]" layout="total, sizes, prev, pager, next, jumper" :total="total"
                        @size-change="handleSizeChange" @current-change="handleCurrentChange" />
                </el-footer>
            </el-container>
        </div>
    </div>
</template>


<script>
    import axios from '@/utils/request'
    import {
        Search,
        Delete,
        Edit,
        CirclePlus
    } from '@element-plus/icons-vue'


    export default {
        name: 'ComplaintView',
        components: {},
        inject: ['reload'],
        setup() {
            return {
                // 
                Search,
                Delete,
                Edit,
                CirclePlus,
            }

        },
        data() {

            return {
                loading: true,
                //
                List: [],
                //
                multipleSelection: [],
                //
                selectDate: ['', ''],
                //
                searchMap: {
                    "startTime": "",
                    "endTime": "",
                    "sel": "",
                    "selcontent": "",
                    "pageNum": 1,
                    "pageSize": 10
                },
                //
                total: 10,
                //
                currentPage: 1,
                // 
                dialogFormVisible: false,
                //
                form: {},
                //
                diaTitle: '',
                //
                dialogVisible: false,
                //
                rules: {
                    buildingName: [{
                        required: true,
                        max: 255,
                        message: '',
                        trigger: 'blur'
                    }],
                    ownerName: [{
                        required: true,
                        max: 255,
                        message: '',
                        trigger: 'blur'
                    }],
                    status: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    description: [{
                        required: false,
                        max: 5000,
                        message: '5000',
                        trigger: 'blur'
                    }],
                    remark: [{
                        required: false,
                        max: 500,
                        message: '500',
                        trigger: 'blur'
                    }],
                },
                // id
                delId: '',
                //id
                delIds: [],
                options2: []
            }
        },
        created() {
            this.search()

        },

        mounted() {
            //sel
            this.searchOptions()

        },

        methods: {
            //+
            search() {
                axios.post("/complaint/selpage", this.searchMap).then((res) => {
                    // console.log(this.searchMap)
                    if (res == null || res.length == 0) this.loading = true
                    else {
                        this.loading = false
                        this.List = res.data
                        this.total = res.total
                        console.log(res)
                    }
                })
            },
            //
            handleSizeChange(pageSize) {
                this.searchMap.pageSize = pageSize;
                this.search();
                console.log('pageSize' + this.searchMap.pageSize)
            },
            //
            handleCurrentChange(pageNum) {
                this.searchMap.pageNum = pageNum;
                this.search();
                console.log('pageNum' + this.searchMap.pageNum)
            },

            //table 
            handleSelectionChange(val) {
                //
                this.multipleSelection = val;
            },
            //
            handleDialog(val, row) {
                if (val == 1) {
                    this.form = []
                    this.diaTitle = ''
                } else {
                    // console.log(JSON.parse(JSON.stringify(row)).id)
                    this.diaTitle = ''
                    //id
                    const id = JSON.parse(JSON.stringify(row)).id
                    axios.get("/complaint/selbyid?id=" + id).then((res) => {
                        this.form = res.data;
                        //
                        this.searchOptions2()
                    });
                }
                this.dialogFormVisible = true
            },
            //
            handleSave() {
                var method = ''
                var obj = {}
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.diaTitle == '') {
                            method = 'add'
                        } else {
                            method = 'update'
                        }
                        obj = {
                            ...this.form
                        }
                        // console.log(obj)
                        axios.post("/complaint/" + method, obj).then((res) => {
                            if (res.flag) {
                                this.$message({
                                    message: res.message,
                                    type: 'success'
                                });
                            } else {
                                this.$message({
                                    message: '',
                                    type: 'error'
                                });
                            }
                            this.reload()
                        });

                        this.reload() // 


                        // this.reload() // 
                        console.log("");
                    } else {
                        console.log("");
                        return false;
                    }
                })


                // this.$router.go(0)
                // this.dialogFormVisible = false
            },
            //
            handleDel() {
                const dataDel = this.multipleSelection
                var id
                //
                // console.log(dataDel.length)
                if (dataDel.length != 0) {
                    dataDel.forEach(item => {
                        // 
                        // console.log(item)
                        // console.log(JSON.parse(JSON.stringify(item)).id)
                        id = JSON.parse(JSON.stringify(item)).id
                        this.delIds.push(id)
                    })
                    axios.post("/complaint/del", this.delIds).then((res) => {
                        if (res.flag) {
                            this.$message({
                                message: res.message,
                                type: 'success'
                            });
                        } else {
                            this.$message({
                                message: '',
                                type: 'error'
                            });
                        }
                        this.reload()
                    });
                    this.reload()
                    this.dialogVisible = false;
                    this.delIds = []
                    this.delId = ''
                }
                //
                else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                }
            },
            //
            handleDelete() {
                const id = JSON.parse(JSON.stringify(this.delId)).id
                this.delIds.push(id)
                axios.post("/complaint/del", this.delIds).then((res) => {
                    if (res.flag) {
                        this.$message({
                            message: res.message,
                            type: 'success'
                        });
                    } else {
                        this.$message({
                            message: '',
                            type: 'error'
                        });
                    }
                    this.reload()
                });
                this.reload()
                this.dialogVisible = false;
                this.delIds = []
                this.delId = ''
            },

            //searcHMap
            dateChange() {
                //
                const daterange = JSON.parse(JSON.stringify(this.selectDate))
                console.log(daterange[0])
                if (daterange == null || daterange.length == 0) {
                    this.searchMap.startTime = '';
                    this.searchMap.endTime = '';
                } else {
                    //searchMap
                    if (daterange[0] != null && daterange[0] != '') {
                        this.searchMap.startTime = daterange[0];
                    }
                    if (daterange[1] != null && daterange[1] != '') {
                        this.searchMap.endTime = daterange[1];
                    }
                }

            },

            //
            searchOptions() {
                axios.get("/building/selall").then((res) => {
                    if (res == null || res.length == 0) this.loading = true
                    else {
                        this.loading = false
                        this.options = res.data
                        // console.log(res.data)
                    }
                })

            },
            //
            searchOptions2() {
                if (this.form.buildingId != null)
                    axios.get("/owner/selbybuildingid?id=" + this.form.buildingId).then((res) => {
                        if (res == null || res.length == 0) this.loading = true
                        else {
                            this.loading = false
                            this.options2 = res.data
                            console.log(res.data)
                        }
                    })
            },
            //idform
            selected() {
                axios.get("/building/selbyid?id=" + this.form.buildingId).then((res) => {
                    this.form.buildingName = res.data.name
                })
                this.searchOptions2()

            },
            //idform
            selected2() {

                axios.get("/owner/selbyid?id=" + this.form.ownerId).then((res) => {
                    this.form.ownerName = res.data.name
                });
            },
            //
            clear() {
                this.options2 = []
                this.form.buildingId = null
                this.form.ownerName = ''
                this.form.ownerId = null
            },

        }
    }
</script>

<style scoped src="@/assets/css/content.css">

</style>
