<template>
    <div class="departmenttype">
        <div class="common-layout">
            <el-container>
                <!--  -->
                <el-header>
                    <div class="head">
                        <!--  -->
                        <el-button type="primary" plain @click="handleDel" :icon="Delete"></el-button>
                        <!--  -->
                        <el-button type="primary" plain @click="handleDialog(1)" :icon="CirclePlus"></el-button>
                        <!--  -->
                        <el-dialog v-model="dialogFormVisible" :title="diaTitle" width="60%">
                            <el-form :model="form" :rules="rules" label-width="30%" ref="form">
                                <el-form-item label="" prop="name">
                                    <el-input v-model="form.name" clearable />
                                </el-form-item>
                                <el-form-item label="" prop="remark">
                                    <el-input v-model="form.remark" type="textarea" :rows="5" />
                                </el-form-item>
                            </el-form>
                            <template #footer>
                                <span class="dialog-footer">
                                    <el-button @click="dialogFormVisible = false"></el-button>
                                    <el-button type="primary" @click="handleSave()">
                                        
                                    </el-button>
                                </span>
                            </template>
                        </el-dialog>

                        <!--  -->
                        <!--                        <el-input class="inpSearch" v-model="searchMap.name" placeholder=""
                            :prefix-icon="Search" clearable @keyup.enter.native="searchMap.pageNum=1;search()" /> -->
                        <el-input class="inpSearch" v-model="searchMap.selcontent" placeholder=""
                            :prefix-icon="Search" clearable @keyup.enter.native="searchMap.pageNum=1;search()">
                            <template #append>
                                <el-select v-model="searchMap.sel" style="width: 150px;background-color: #f3efff;">
                                    <el-option label="" value="name" />
                                </el-select>
                            </template>
                        </el-input>
                        <!--  -->
                        <div class="date">
                            <el-date-picker v-model="selectDate" type="daterange" range-separator=""
                                start-placeholder="" end-placeholder="" size="large" @change="dateChange"
                                value-format="YYYY-MM-DD hh:mm:ss" />
                        </div>
                        <!--  -->
                        <el-button type="primary" plain @click="searchMap.pageNum=1;search()" :icon="Search">
                        </el-button>
                    </div>
                </el-header>
                <!--  -->
                <el-main>
                    <!--  -->
                    <el-table v-loading="loading" element-loading-text="..." :data="List" border size="large"
                        @selection-change="handleSelectionChange" :header-cell-style="{
                            'background-color': '#d4cdf0',
                            'color': '#877fa6',
                            'border-bottom': '1px #877fa6 solid'
                        }" :default-sort="{prop: 'id', order: 'ascending'}">
                        <el-table-column width="50" type="selection" />
                        <el-table-column fixed prop="id" label="ID" width="80" sortable />
                        <el-table-column prop="name" label="" width="300" />
                        <el-table-column prop="remark" label="" width="526" />
                        <el-table-column prop="createTime" label="" width="180" sortable />
                        <el-table-column fixed="right" label="" width="120">
                            <template #default="scope">
                                <!--  -->
                                <el-button class="btnEsit" link @click="handleDialog(2,scope.row)" :icon="Edit">
                                </el-button>
                                <!--  -->
                                <el-button class="btnDel" link @click="delId=scope.row;dialogVisible = true;"
                                    :icon="Delete">
                                </el-button>

                                <!---->
                                <el-dialog v-model="dialogVisible" title="" width="30%" :modal="false"
                                    :show-close="false">
                                    <span></span>
                                    <template #footer>
                                        <span class="dialog-footer">
                                            <el-button @click="dialogVisible = false"></el-button>
                                            <el-button type="primary" @click="handleDelete()">
                                                
                                            </el-button>
                                        </span>
                                    </template>
                                </el-dialog>

                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
                <!--  -->
                <el-footer>
                    <!--  -->
                    <el-pagination v-model:currentPage="currentPage" v-model:page-size="searchMap.pageSize"
                        :page-sizes="[5,10,20,50]" layout="total, sizes, prev, pager, next, jumper" :total="total"
                        @size-change="handleSizeChange" @current-change="handleCurrentChange" />
                </el-footer>
            </el-container>
        </div>
    </div>
</template>


<script>
    import axios from '@/utils/request'
    import {
        Search,
        Delete,
        Edit,
        CirclePlus
    } from '@element-plus/icons-vue'

    export default {
        name: 'ChargeTypeView',
        components: {},
        inject: ['reload'],
        setup() {
            return {
                // 
                Search,
                Delete,
                Edit,
                CirclePlus
            }
        },
        data() {

            return {
                loading: true,
                //
                List: [],
                //
                multipleSelection: [],
                //
                selectDate: ['', ''],
                //
                searchMap: {
                    "startTime": "",
                    "endTime": "",
                    "sel": "",
                    "selcontent": "",
                    "pageNum": 1,
                    "pageSize": 10
                },
                //
                total: 10,
                //
                currentPage: 1,
                // 
                dialogFormVisible: false,
                //
                form: {},
                //
                diaTitle: '',
                //
                dialogVisible: false,
                //
                rules: {
                    name: [{
                        required: true,
                        max: 255,
                        message: '',
                        trigger: 'blur'
                    }],
                    remark: [{
                        required: false,
                        max: 500,
                        message: '500',
                        trigger: 'blur'
                    }],
                },
                // id
                delId: '',
                //id
                delIds: [],
            }
        },
        created() {
            this.search()
        },

        methods: {
            //+
            search() {
                axios.post("/chargetype/selpage", this.searchMap).then((res) => {
                    // console.log(this.searchMap)
                    if (res == null || res.length == 0) this.loading = true
                    else {
                        this.loading = false
                        this.List = res.data
                        this.total = res.total
                        console.log(res)
                    }
                })
            },
            //
            handleSizeChange(pageSize) {
                this.searchMap.pageSize = pageSize;
                this.search();
                console.log('pageSize' + this.searchMap.pageSize)
            },
            //
            handleCurrentChange(pageNum) {
                this.searchMap.pageNum = pageNum;
                this.search();
                console.log('pageNum' + this.searchMap.pageNum)
            },

            //table 
            handleSelectionChange(val) {
                //
                this.multipleSelection = val;
            },
            //
            handleDialog(val, row) {
                if (val == 1) {
                    this.form = []
                    this.diaTitle = ''
                } else {
                    // console.log(JSON.parse(JSON.stringify(row)).id)
                    this.diaTitle = ''
                    //id
                    const id = JSON.parse(JSON.stringify(row)).id
                    axios.get("/chargetype/selbyid?id=" + id).then((res) => {
                        this.form = res.data;
                    });
                }
                this.dialogFormVisible = true
            },
            //
            handleSave() {
                var method = ''
                var obj = {}
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.diaTitle == '') {
                            method = 'add'
                        } else {
                            method = 'update'
                        }
                        obj = {
                            ...this.form
                        }
                        // console.log(obj)
                        axios.post("/chargetype/" + method, obj).then((res) => {
                            if (res.flag) {
                                this.$message({
                                    message: res.message,
                                    type: 'success'
                                });
                            } else {
                                this.$message({
                                    message: '',
                                    type: 'error'
                                });
                            }
                            this.reload()
                        });

                        this.reload() // 


                        // this.reload() // 
                        console.log("");
                    } else {
                        console.log("");
                        return false;
                    }
                })


                // this.$router.go(0)
                // this.dialogFormVisible = false
            },
            //
            handleDel() {
                const dataDel = this.multipleSelection
                var id
                //
                // console.log(dataDel.length)
                if (dataDel.length != 0) {
                    dataDel.forEach(item => {
                        // 
                        // console.log(item)
                        // console.log(JSON.parse(JSON.stringify(item)).id)
                        id = JSON.parse(JSON.stringify(item)).id
                        this.delIds.push(id)
                    })
                    axios.post("/chargetype/del", this.delIds).then((res) => {
                        if (res.flag) {
                            this.$message({
                                message: res.message,
                                type: 'success'
                            });
                        } else {
                            this.$message({
                                message: '',
                                type: 'error'
                            });
                        }
                        this.reload()
                    });
                    this.reload()
                    this.dialogVisible = false;
                    this.delIds = []
                    this.delId = ''
                }
                //
                else{
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                }
            },
            //
            handleDelete() {
                const id = JSON.parse(JSON.stringify(this.delId)).id
                this.delIds.push(id)
                axios.post("/chargetype/del", this.delIds).then((res) => {
                    if (res.flag) {
                        this.$message({
                            message: res.message,
                            type: 'success'
                        });
                    } else {
                        this.$message({
                            message: '',
                            type: 'error'
                        });
                    }
                    this.reload()
                });
                this.reload()
                this.dialogVisible = false;
                this.delIds = []
                this.delId = ''
            },

            //searcHMap
            dateChange() {
                //
                const daterange = JSON.parse(JSON.stringify(this.selectDate))
                console.log(daterange[0])
                if (daterange == null || daterange.length == 0) {
                    this.searchMap.startTime = '';
                    this.searchMap.endTime = '';
                } else {
                    //searchMap
                    if (daterange[0] != null && daterange[0] != '') {
                        this.searchMap.startTime = daterange[0];
                    }
                    if (daterange[1] != null && daterange[1] != '') {
                        this.searchMap.endTime = daterange[1];
                    }
                }

            },
            //
            // dateFormat1(val) {
            //     if (val) {
            //         if (val.indexOf('T') === -1) return val
            //         const arr = val.split('T')
            //         const d = arr[0]
            //         const darr = d.split('-')
            //         const t = arr[1]
            //         const tarr = t.split('.000')
            //         const marr = tarr[0].split(':')
            //         const dd = darr[0] + '-' + darr[1] + '-' + darr[2] + ' ' + marr[0] + ':' + marr[1] + ':' + marr[2]
            //             .substring(0, 2)
            //         return dd
            //     }
            // },
            //
            // dateFormat(row, column, cellValue, index) {
            //     const daterc = row[column.property]
            //     // console.log(row, column)
            //     if (daterc) {
            //         if (daterc.indexOf('T') === -1) return daterc
            //         const arr = daterc.split('T')
            //         const d = arr[0]
            //         const darr = d.split('-')
            //         const t = arr[1]
            //         const tarr = t.split('.000')
            //         const marr = tarr[0].split(':')
            //         const dd = darr[0] + '-' + darr[1] + '-' + darr[2] + ' ' + marr[0] + ':' + marr[1] + ':' + marr[2]
            //             .substring(0, 2)
            //         return dd
            //     }
            // },


        }
    }
</script>

<style scoped src="@/assets/css/content.css">

</style>
